* {
  box-sizing: border-box;
}

body {
  background-color: rgba(238, 238, 238, .933);
}

.cat-img {
  width: 500px;
  height: 100%;
  display: block;
}

.error, .loader {
  font-weight: 700;
}

.cat-info {
  border: 1px solid;
  border-radius: 0 0 4px 4px;
  column-gap: 18px;
  padding: 5px;
  display: flex;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

.description {
  max-width: 50%;
  font-size: 22px;
  font-weight: 400;
}

.breed-select {
  width: 200px;
  height: 25px;
  font-weight: 700;
  box-shadow: inset -2px -2px 4px rgba(255, 255, 255, .1), inset 2px 2px 4px rgba(0, 0, 0, .75);
}

